import React, { useState } from "react";

function ModalInitialInformation(){

    const [show, setShow] = useState(true);

    return (<div className="modalInfo" hidden={!show}>

    <h2>Tutorial</h2>
    <p>
      GamePixels se trata de darle clics a pxeles para borrarlos en el menor tiempo posible.
    </p>
    <h3>Tipos de pxeles</h3>
    <div className="pixel-info heavy">
      <h4><div className="pixel"></div>Heavy</h4>
      <p>Cada cierto tiempo se vuelve ms fuerte, por lo que si lo dejas tranquilo habr que darle ms clics.</p>
    </div>
    <div className="pixel-info guardian">
      <h4><div className="pixel"></div>Guardian</h4>
      <p>Tiene una barrera alrededor de un pixel central que si no eliminas primero, no podrs borrar el ncleo.</p>
    </div>
    <div className="pixel-info speed">
      <h4><div className="pixel"></div>Speed</h4>
      <p>Se teletransporta y cambia de posicin rpidamente.</p>
    </div>
    <div className="pixel-info mutant">
      <h4><div className="pixel"></div>Mutant</h4>
      <p>Va generando copias alrededor de s mismo.</p>
    </div>
    <div className="pixel-info psycho">
      <h4><div className="pixel"></div>Psycho</h4>
      <p>Tiene varias copias falsas ya puestas, si eliminas la verdadera, todas las dems se eliminan.</p>
    </div>

        <div className="quit-modal" onClick={()=>setShow(v=>!v)}>Click aqu para ir al juego</div>

    </div>);

}

export {

    ModalInitialInformation

}